import React, { useContext, useState } from "react";
import {
  FormControl,
  InputLabel,
  makeStyles,
  MenuItem,
  Select,
} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { ApplicationContext } from "../ApplicationContext.js";
import * as S from "./ModalAddRefundSelected.style.js";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(4),
    display: "flex",
    gap: "32px",
  },
}));

const ModalAddRefundSelected = ({ setOficialMultiTrecho }) => {
  const { showRefundModalSelected, setShowRefundModalSelected } =
    useContext(ApplicationContext);

  const classes = useStyles();

  // const [oficialMultiTrecho, setOficialMultiTrecho] = useState(multiTrecho);

  const handleCloseModal = () => {
    setShowRefundModalSelected((old) => !old);
  };

  const handleAddRefundSelected = () => {
    if (selectedDiscount) {
      let discount =
        selectedDiscount === 1
          ? 0.15
          : selectedDiscount === 2
          ? 0.25
          : selectedDiscount === 3
          ? 0.45
          : selectedDiscount === 4
          ? 1
          : undefined;

      setOficialMultiTrecho((old) =>
        old.map((item) =>
          item.checked
            ? {
                ...item,
                reembolsoManual: item.passagem * discount,
              }
            : { ...item }
        )
      );
    }
    setShowRefundModalSelected((old) => !old);
  };

  const [selectedDiscount, setSelectedDiscount] = useState(undefined);
  const handleChangeDiscount = (event) => {
    setSelectedDiscount(event.target.value);
  };
  return (
    <>
      <S.Mask showRefundModalSelected={showRefundModalSelected}></S.Mask>
      <S.Container
        showRefundModalSelected={showRefundModalSelected}
        className="container"
      >
        <header className="header-modal">
          <h1>
            {showRefundModalSelected === 1
              ? "Adicionar reembolso para todos selecionados"
              : "Adicionar voucher para todos selecionados"}
          </h1>
        </header>

        <div
          className={classes.root + " content content-general"}
          style={{ height: "100%;" }}
        >
          <Grid container xs={12} alignContent="flex-start">
            <Grid item xs={12}>
              <FormControl className="w-100">
                <InputLabel id="demo-simple-select-label">
                  Procentagem
                </InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  value={selectedDiscount}
                  onChange={handleChangeDiscount}
                >
                  <MenuItem value={1}>15%</MenuItem>
                  <MenuItem value={2}>25%</MenuItem>
                  <MenuItem value={3}>45%</MenuItem>
                  <MenuItem value={4}>100%</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>

          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button
              className="btn btn-primary"
              onClick={handleAddRefundSelected}
            >
              Adicionar
            </button>
          </footer>
        </div>
      </S.Container>
    </>
  );
};

export default ModalAddRefundSelected;
