import React, { useContext, useEffect, useRef, useState } from "react";
import {
  Box,
  Button,
  ClickAwayListener,
  FormControl,
  Grid,
  Grow,
  InputLabel,
  makeStyles,
  MenuItem,
  MenuList,
  Paper,
  Popper,
  Select,
  TextField,
} from "@material-ui/core";
import { ApplicationContext } from "../ApplicationContext.js";
import { MdArrowDropDown } from "react-icons/md";
import ListPax from "../components/ListPax";
import * as S from "./ModalAddRefundGroup.style.js";
import ModalAddRefundSelected from "./ModalAddRefundSelected.js";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(4),
    paddingRight: "0px",
    display: "flex",
    gap: "32px",
  },
}));

const ModalAddRefundGroup = ({
  id,
  multiTrecho,
  multiTrechoID,
  refundIdIndexGroup,
}) => {
  const {
    setShowRefundModal,
    showRefundModal,
    allRefundsGroup,
    setAllRefundsGroup,
    setShowRefundModalSelected,
    showRefundModalSelected,
  } = useContext(ApplicationContext);

  const classes = useStyles();

  const [objToWork, setObjToWork] = useState(() => {
    if (refundIdIndexGroup) {
      const onlyRefundsFromId = allRefundsGroup.filter(
        (refund) => refund.id === id
      );
      const onyThisRefundInfo = onlyRefundsFromId.filter(
        (refund) => refund.idIndex === refundIdIndexGroup
      );
      return onyThisRefundInfo[0];
    } else {
      return false;
    }
  });

  const [oficialMultiTrecho, setOficialMultiTrecho] = useState(
    objToWork ? objToWork.pax : multiTrecho
  );

  const [total, setTotal] = useState(() => (objToWork ? objToWork.total : "0"));

  const handleCloseModal = () => {
    setShowRefundModal((old) => !old);
  };

  const [descontoTempoParado, setDescontoTempoParado] = useState(
    objToWork ? objToWork.descontoTempoParado : "0%"
  );

  const [tempoParado, setTempoParado] = useState(
    objToWork ? objToWork.tempoParado : ""
  );
  const handleTempoParado = (event) => {
    setTempoParado(event.target.value);
  };

  const [description, setDescription] = useState(
    objToWork ? objToWork.description : ""
  );
  const handleDescription = (event) => {
    setDescription(event.target.value);
  };

  const calcTotal = (newMultiTrecho) => {
    const allValues = newMultiTrecho.map((item) => {
      return [
        item.reembolsoTempo,
        item.voucher,
        item.lanche,
        item.reembolsoManual,
      ];
    });

    const flatValues = allValues.flat(Infinity);

    const total = flatValues.reduce((a, b) => {
      const aString = `${a}`;
      const bString = `${b}`;

      return (
        parseFloat(aString.replace(",", ".")) +
        parseFloat(bString.replace(",", "."))
      );
    });

    setTotal(total);
  };

  useEffect(() => {
    calcTotal(oficialMultiTrecho);
  }, [oficialMultiTrecho]);

  useEffect(() => {
    const horaMinuto = tempoParado.split(":");
    const hora = horaMinuto[0];

    if (hora >= 2) {
      setOficialMultiTrecho((old) =>
        old.map((item) => ({ ...item, reembolsoTempo: item.passagem * 0.3 }))
      );
      setDescontoTempoParado("30%");
    } else {
      setDescontoTempoParado("0%");
      setOficialMultiTrecho((old) =>
        old.map((item) => ({ ...item, reembolsoTempo: 0 }))
      );
    }
  }, [tempoParado, setOficialMultiTrecho]);

  //Save
  const handleAddRefund = () => {
    const obj = {
      id: id,
      idIndex: Date.now(),
      tempoParado,
      descontoTempoParado,
      description,
      total,
      pax: oficialMultiTrecho,
    };

    if (refundIdIndexGroup) {
      const newRefunds = allRefundsGroup.map((refund) =>
        refund.idIndex === refundIdIndexGroup ? { ...obj } : { ...refund }
      );
      setAllRefundsGroup(newRefunds);
      window.localStorage.setItem(
        "allRefundsGroup",
        JSON.stringify(newRefunds)
      );
    } else {
      const newRefunds = [...allRefundsGroup, obj];
      setAllRefundsGroup(newRefunds);
      window.localStorage.setItem(
        "allRefundsGroup",
        JSON.stringify(newRefunds)
      );
    }

    handleCloseModal();
  };

  const [open, setOpen] = useState(false);
  const anchorRef = useRef(null);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    setShowRefundModalSelected(event.target.value);
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }
  const prevOpen = useRef(open);
  useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  return (
    <>
      {showRefundModalSelected && (
        <ModalAddRefundSelected setOficialMultiTrecho={setOficialMultiTrecho} />
      )}
      <S.Mask showRefundModal={showRefundModal}></S.Mask>
      <S.Container showRefundModal={showRefundModal} className="container">
        <header className="header-modal">
          <h1>Adicionar reembolso ao grupo</h1>
        </header>

        <div
          className={classes.root + " content content-general"}
          style={{ height: "100%;" }}
        >
          <Grid container spacing={3} xs={2} alignContent="flex-start">
            <Grid item xs={12}>
              <h3>Opções</h3>
            </Grid>
            <Grid item xs={12} style={{ marginTop: "4px" }} disabled>
              <FormControl fullWidth={true}>
                <InputLabel id="demo-simple-select-label">
                  Multi-trecho
                </InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={multiTrechoID}
                  disabled
                >
                  <MenuItem value={0}>CGR - NIU</MenuItem>
                  <MenuItem value={1}>JRD - PMU</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                className={"w-100"}
                label="Tempo parado (horas)"
                type="time"
                value={tempoParado}
                onChange={(event) => handleTempoParado(event)}
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={{
                  step: 300, // 5 min
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="standard-basic"
                className={"w-100 noevents"}
                label="Reembolso por tempo parado"
                value={descontoTempoParado}
              />
            </Grid>

            <Grid item xs={12} style={{ marginTop: "8px" }}>
              <h3>Descreva à experiência dos passageiros:</h3>
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="standard-multiline-static"
                label="Detalhes"
                className={"w-100"}
                multiline
                rows={4}
                defaultValue="Escreva aqui..."
                value={description}
                onChange={handleDescription}
              />
              <a
                href="https://docs.google.com/spreadsheets/d/1zZQH5O0VFwAPmWdgJPEe7ZNkPq7s_RDNv9Xemh522yA/edit#gid=1630210288"
                target="_Blank"
                rel="noreferrer"
                style={{ marginTop: "16px", display: "block" }}
              >
                Política de reembolso
              </a>
            </Grid>
          </Grid>
          <Grid
            container
            spacing={3}
            xs={10}
            alignContent="flex-start"
            style={{ marginTop: "-38px" }}
          >
            <Grid item xs={12}>
              <Box
                item
                display="flex"
                alignItems="flex-end"
                justifyContent="space-between"
              >
                <Grid item xs={4}>
                  <h3>Lista de passageiros</h3>
                </Grid>
                <Grid item xs={2}>
                  {/* <FormControl className="w-100">
                    <InputLabel id="demo-simple-select-label">Mais</InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={selectedAction}
                      onChange={handleChangeSelectedAction}
                    >
                      <MenuItem value={1}>
                        Adicionar reembolso para todos selecionados
                      </MenuItem>
                      <MenuItem value={2}>
                        Adicionar voucher para todos selecionados
                      </MenuItem>
                    </Select>
                  </FormControl> */}
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "flex-end",
                      alignItems: "center",
                    }}
                  >
                    <Button
                      ref={anchorRef}
                      aria-controls={open ? "menu-list-grow" : undefined}
                      aria-haspopup="true"
                      onClick={handleToggle}
                      style={{ textTransform: "capitalize" }}
                    >
                      Mais
                      <MdArrowDropDown />
                    </Button>
                    <Popper
                      open={open}
                      anchorEl={anchorRef.current}
                      role={undefined}
                      transition
                      disablePortal
                      style={{ zIndex: 100 }}
                    >
                      {({ TransitionProps, placement }) => (
                        <Grow
                          {...TransitionProps}
                          style={{
                            transformOrigin:
                              placement === "bottom"
                                ? "center top"
                                : "center bottom",
                          }}
                        >
                          <Paper>
                            <ClickAwayListener onClickAway={handleClose}>
                              <MenuList
                                autoFocusItem={open}
                                id="menu-list-grow"
                                onKeyDown={handleListKeyDown}
                              >
                                <MenuItem onClick={handleClose} value={1}>
                                  Adicionar reembolso para todos selecionados
                                </MenuItem>
                              </MenuList>
                            </ClickAwayListener>
                          </Paper>
                        </Grow>
                      )}
                    </Popper>
                  </div>
                </Grid>
              </Box>
            </Grid>
            <Grid item xs={12} flex={2} style={{ height: "100%" }}>
              <ListPax
                total={total}
                setTotal={setTotal}
                multiTrecho={oficialMultiTrecho}
                setMultiTrecho={setOficialMultiTrecho}
              />
            </Grid>
          </Grid>

          <footer className="footer">
            <button className="btn btn-close" onClick={handleCloseModal}>
              Fechar
            </button>
            <button className="btn btn-primary" onClick={handleAddRefund}>
              Salvar
            </button>
          </footer>
        </div>
      </S.Container>
    </>
  );
};

export default ModalAddRefundGroup;
